import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import SalesInvoiceCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/sales/salesInvoice/actions";
import { Box } from "../../../App/app.style";
import { removeSaveData } from "../../../../helpers/helper";

const redirecTo = "/dashboard/sales/sales-invoices";

class CreateSalesInvoice extends Component {
  componentDidMount() {
    const { createSalesInvoice } = this.props;
    createSalesInvoice();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.salesInvoice.error) {
      notification("error", this.props.salesInvoice.error);
      this.props.clearError();
    }
    if (
      (this.props.salesInvoice.success || this.props.salesInvoice.warning) &&
      this.props.salesInvoice.id_sales_invoice
    ) {
      this.props.history.replace(
        redirecTo + "/update/" + this.props.salesInvoice.id_sales_invoice
      );
      // this.props.history.replace(redirecTo);
      // notification("success", this.props.salesInvoice.success);
      // this.props.clearSuccess();
    }
  }

  handleSubmit = value => {
    const { storeSalesInvoice } = this.props;
    storeSalesInvoice(value);
    // console.log(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="salesInvoices.button.add" />}>
            <Button
              onClick={() => {
                removeSaveData("si");
                this.props.history.replace(redirecTo);
              }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <SalesInvoiceCreateForm
            submit={this.handleSubmit}
            salesInvoice={this.props.salesInvoice}
            history={this.props.history}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    salesInvoice: state.salesInvoice,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(CreateSalesInvoice);
