import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form } from "antd";
import EditableCell from "./editableCellFreightCost";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper from "../../../App/app.style";
import ModalExpenseDetail from "./modalExpenseDetail";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

const getTotal = arr => {
  let totalCurrencyAmount = 0;
  let totalAmount = 0;
  arr.map(data => {
    // totalCurrencyAmount += parseFloat(data.currency_amount);
    totalAmount += parseFloat(data.amount);
  });

  return {
    totalCurrencyAmount: totalCurrencyAmount,
    totalAmount: totalAmount
  };
};

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { freightCosts } = props;

    this.state = {
      dataSource: [...freightCosts],
      count: freightCosts.length + 1,
      // totalCurrencyAmount: 0,
      totalAmount: 0,
      visible: false,
      selectedItem: {}
    };
  }

  componentDidMount() {
    const { freightCost, freightCosts, clearFreightCost, clearFreightCosts } = this.props;

    // milih freightCost sendiri
    if (freightCost) {
      const {
        dataSource,
        count,
        totalCurrencyAmount,
        totalAmount
      } = this.state;

      const find = dataSource.findIndex(
        currData => currData.id_account === freightCost.id_account
      );

      // freightCost baru
      if (find < 0) {
        const init = {
          index_no: count,
          amount: 0,
          // currency_amount: 0,
          id_department: null,
          id_project: null
        };

        const newData = Object.assign({}, freightCost, init);
        // console.log(newData);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          totalCurrencyAmount: totalCurrencyAmount,
          totalAmount: totalAmount
        });

        clearFreightCost(
          [...dataSource, newData],
          totalCurrencyAmount,
          totalAmount
        );
      }
    }
    // jika freightCost dari transaksi sebelumnya
    else if (freightCosts.length !== 0) {
      // let newTotCurrAmount = 0;
      let newTotAmount = 0;
      let newIndex = 0;

      freightCosts.map(currData => {
        // newTotCurrAmount += parseFloat(currData.currency_amount);
        newTotAmount += parseFloat(currData.amount);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...freightCosts],
        // totalCurrencyAmount: newTotCurrAmount,
        totalAmount: newTotAmount,
        count: newIndex
      });
      clearFreightCosts(freightCosts, newTotAmount);
    }
  }

  componentDidUpdate(prevProps) {
    const {
      freightCost,
      freightCosts,
      currency_rate,
      handleSaveExpenseList,
      clearFreightCost,
      clearFreightCosts
    } = this.props;

    // milih freightCost sendiri
    if (freightCost) {
      const {
        dataSource,
        count,
        // totalCurrencyAmount,
        totalAmount
      } = this.state;

      const find = dataSource.findIndex(
        currData => currData.id_account === freightCost.id_account
      );

      // freightCost baru
      if (find < 0) {
        const init = {
          index_no: count,
          amount: 0,
          // currency_amount: 0,
          id_department: null,
          id_project: null
        };

        const newData = Object.assign({}, freightCost, init);
        // console.log(newData);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          // totalCurrencyAmount: totalCurrencyAmount,
          totalAmount: totalAmount
        });

        clearFreightCost(
          [...dataSource, newData],
          // totalCurrencyAmount,
          totalAmount
        );
      }
    }
    // jika freightCost dari transaksi sebelumnya
    else if (freightCosts.length !== 0) {
      // let newTotCurrAmount = 0;
      let newTotAmount = 0;
      let newIndex = 0;

      freightCosts.map(currData => {
        // newTotCurrAmount += parseFloat(currData.currency_amount);
        newTotAmount += parseFloat(currData.amount);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...freightCosts],
        // totalCurrencyAmount: newTotCurrAmount,
        totalAmount: newTotAmount,
        count: newIndex
      });
      clearFreightCosts(freightCosts, newTotAmount);
    }
    // Jika currency rate berubah
    // else if (prevProps.currency_rate !== currency_rate) {
    //   const { totalCurrencyAmount } = this.state;
    //   let newData = this.state.dataSource;
    //   let newTotAmount = 0;
    //   const rate = parseFloat(currency_rate);

    //   newData.map(data => {
    //     data.amount = parseFloat(data.currency_amount) * rate;
    //     newTotAmount += data.amount;
    //   });
    //   this.setState({
    //     dataSource: [...newData],
    //     totalAmount: newTotAmount
    //   });
    //   handleSaveExpenseList(newData, totalCurrencyAmount, newTotAmount);
    // }
  }

  handleExpenseDetailModal = row => {
    const { visible } = this.state;
    // console.log(row);

    visible
      ? this.setState({ visible: false, selectedItem: {} })
      : this.setState({ visible: true, selectedItem: row });
  };

  handleExpenseDetail = value => {
    this.setState({ visible: false, selectedItem: {} });
    this.handleSave(value);
  };

  handleDelete = index_no => {
    const { handleSaveExpenseList } = this.props;
    // let newTotCurrAmount = 0;
    let newTotAmount = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      // newTotCurrAmount += parseFloat(currData.currency_amount);
      newTotAmount += parseFloat(currData.amount);
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      // totalCurrencyAmount: newTotCurrAmount,
      totalAmount: newTotAmount
    });
    handleSaveExpenseList(newData, newTotAmount, true);
  };

  handleSave = row => {
    const { handleSaveExpenseList } = this.props;
    const newData = this.state.dataSource;
    // const rate = currency_rate ? parseFloat(currency_rate) : 1;
    // let newTotCurrAmount = 0;
    let newTotAmount = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    row.amount = parseFloat(row.amount);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    newData.map(currData => {
      // newTotCurrAmount += parseFloat(currData.currency_amount);
      newTotAmount += parseFloat(currData.amount);
    });

    // console.log(row);

    this.setState({
      dataSource: [...newData],
      // totalCurrencyAmount: newTotCurrAmount,
      totalAmount: newTotAmount
    });
    handleSaveExpenseList(newData, newTotAmount, true);
  };

  render() {
    // console.log(this.state.dataSource);

    const { dataSource, visible, selectedItem } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        width: "5%",
        align: "right",
        render: (text, row) => {
          if (isNaN(text)) {
            return {
              children: text,
              props: {
                colSpan: 3
              }
            };
          } else {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          }
        }
      },
      {
        title: getLanguage({ id: "accountCode" }),
        dataIndex: "account_code",
        width: "25%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account_name",
        width: "40%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "amount" }),
        dataIndex: "amount",
        width: "25%",
        align: "right",
        editable: true,
        onCell: record => {
          if (isNaN(record.index_no)) {
            return {
              record,
              dataIndex: "amount",
              title: getLanguage({ id: "amount" }),
              width: "15%",
              status: this.props.status
            };
          } else {
            return {
              record,
              editable: true,
              dataIndex: "amount",
              title: getLanguage({ id: "amount" }),
              width: "15%",
              handleSave: this.handleSave,
              status: this.props.status
            };
          }
        }
      },
      // {
      //   title: "Base Amount",
      //   width: "15%",
      //   dataIndex: "amount",
      //   align: "left",
      //   render: (text, row) => {
      //     const val = <NumberFormat value={text} />
      //     if (isNaN(row.index_no)) {
      //       return {
      //         children: text,
      //         props: {
      //           colSpan: 2
      //         }
      //       };
      //     } else {
      //       return val;
      //     }
      //   }
      // },
      {
        title: "",
        dataIndex: "operation",
        width: "5%",
        align: "center",
        // fixed: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 1
                // fixed: null
              }
            };
          }
          if (!this.props.status || this.props.status === 1) {
            return (
              <div>
                <Tooltip
                  placement="top"
                  title={<IntlMessages id="detail" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceEdtBtn"
                    size="small"
                    onClick={() => this.handleExpenseDetailModal(row)}
                  >
                    <i className="ion-navicon-round" />
                  </Button>
                </Tooltip>
                <Popconfirm
                  title={<IntlMessages id="confirmDelete" />}
                  onConfirm={() => this.handleDelete(row.index_no)}
                  okText={<IntlMessages id="yes" />}
                  cancelText={<IntlMessages id="no" />}
                >
                  <Tooltip
                    title={<IntlMessages id="delete" />}
                  >
                    <Button
                      //style={{ borderRadius: "50%" }}
                      className="invoiceDltBtn"
                      size="small"
                    >
                      <i className="ion-trash-b" />
                    </Button>
                  </Tooltip>
                </Popconfirm>       
              </div>
            );
          } else {
            return (
              <Tooltip
                placement="top"
                title={<IntlMessages id="detail" />}
              >
                <Button
                  //style={{ borderRadius: "50%" }}
                  className="invoiceEdtBtn"
                  size="small"
                  onClick={() => this.handleExpenseDetailModal(row)}
                >
                  <i className="ion-navicon-round" />
                </Button>
              </Tooltip>
            );
          }
        }
      }
    ];

    const total = getTotal(dataSource);
    const info = {
      index_no: getLanguage({ id: "total" }),
      // currency_amount: <NumberFormat value={total.totalCurrencyAmount} />,
      amount: <NumberFormat value={total.totalAmount} />
    };
    const modifiedData = dataSource.length > 0 ? [...dataSource, info] : [];
    return (
      <CardWrapper>
        <div>
          <ModalExpenseDetail
            visible={visible}
            selectedData={selectedItem}
            status={this.props.status}
            handleExpenseDetailModal={this.handleExpenseDetailModal}
            handleExpenseDetail={this.handleExpenseDetail}
            submit={this.handleExpenseDetail}
            departments={this.props.departments}
            projects={this.props.projects}
          />

          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper              
              className="invoiceListTable"
              components={components}
              rowKey={record => record.index_no}
              rowClassName={() => "editable-row"}
              dataSource={modifiedData}
              columns={columns}
              pagination={false}
            />
          </Scrollbars>
        </div>
      </CardWrapper>
    );
  }
}

export default EditableTable;
