import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/sales/salesInvoice/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";

import {
  Modal,
  Tooltip,
  Table,
  Collapse,
  Select,
  DatePicker,
  Input,
  Row,
  Col,
  Empty
} from "antd";
import { StatusTag, customCollapseStyle, StatusBadge, TransNo } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;
const Search = Input.Search;
const { getDeliveryOrders } = actions;

const expandedRowRender = data => {
  const columns = [
    {
      title: getLanguage({ id: "no." }),
      dataIndex: "index_no",
      width: "5%"
    },
    {
      title: getLanguage({ id: "code" }),
      dataIndex: "item_code",
      width: "15%",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "itemName" }),
      dataIndex: "item_name",
      width: "30%",
      render: text => {
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="salesOrderQuantity" />}>
          <a href="javascript:;"><IntlMessages id="soQty" /></a>
        </Tooltip>
      ),
      dataIndex: "ordered_qty", // dari PR
      width: "10%",
      align: "center",
      render: text => <NumberFormat value={text} />
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="leftQuantity" />}>
          <a href="javascript:;"><IntlMessages id="leftQty" /></a>
        </Tooltip>
      ),
      dataIndex: "left_qty", // dari PR
      width: "10%",
      align: "center",
      render: text => <NumberFormat value={text} />
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="deliveryQuantity" />}>
          <a href="javascript:;"><IntlMessages id="deliveryQty" /></a>
        </Tooltip>
      ),
      dataIndex: "delivery_qty", // dari PR
      width: "10%",
      align: "center",
      render: (text, row) => <><NumberFormat value={text} /> {row.unit_code}</>
    },
    {
      title: (
        <Tooltip placement="top" title={<IntlMessages id="baseQuantity" />}>
          <a href="javascript:;"><IntlMessages id="baseQty" /></a>
        </Tooltip>
      ),
      dataIndex: "base_qty", // dari PR
      width: "10%",
      align: "center",
      render: (text, row) => <><NumberFormat value={text} /> {row.base_unit_code}</>
    }
  ];

  const dataSource = data;
  return (
    <div>
      <strong>
        <IntlMessages id="purchaseInvoices.importPR.itemList" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={true}
        style={{ marginTop: "24px" }}
      />
    </div>
  );
};

class ModalImportDO extends Component {
  state = {
    filter: {
      idCustomer: this.props.idCustomer,
      idDeliveryLocation: null,
      idLocation: this.props.idLocation,
      idSalesOrder: null,
      status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  componentDidUpdate(prevProps) {
    const { idLocation, idCustomer } = this.props;

    if (
      prevProps.idLocation !== idLocation &&
      prevProps.idCustomer !== idCustomer &&
      idLocation &&
      idCustomer
    ) {
      this.handleLocationSupplierChange(idLocation, idCustomer);
      this.setState({
        filter: {
          ...this.state.filter,
          idCustomer,
          idLocation
        }
      });
    } else if (prevProps.idLocation !== idLocation && idCustomer) {
      this.handleLocationSupplierChange(idLocation, idCustomer);
      this.setState({
        filter: {
          ...this.state.filter,
          idCustomer,
          idLocation
        }
      });
    } else if (prevProps.idCustomer !== idCustomer && idLocation) {
      this.handleLocationSupplierChange(idLocation, idCustomer);
      this.setState({
        filter: {
          ...this.state.filter,
          idCustomer,
          idLocation
        }
      });
    }
  }

  handleLocationSupplierChange = (id_location, id_customer) => {
    const { getDeliveryOrders } = this.props;
    const string =
      "id_location=" + id_location + "&id_customer=" + id_customer;

    getDeliveryOrders(1, null, string);
  };

  handleLocationChange = id_location => {
    // const { getDeliveryOrders } = this.props;
    const { id_customer } = this.state.filter;
    let string = "id_delivery_location=" + id_location;
    if (id_customer) {
      string += "&id_customer=" + id_customer;
    }
    // getDeliveryOrders(1, null, string);
  };

  handleSupplierChange = id_customer => {
    // const { getDeliveryOrders } = this.props;
    const { idLocation } = this.state.filter;
    let string = "id_customer=" + id_customer;
    if (idLocation) {
      string += "&id_location=" + idLocation;
    }
    // getDeliveryOrders(1, null, string);
  };

  handleSelect = value => {
    const { handleSelectModal } = this.props;
    handleSelectModal(value);
  };

  // Handle search
  handleSearch = value => {
    const { getDeliveryOrders, idCustomer, idLocation } = this.props;
    const string = "id_customer=" + idCustomer + "&id_location=" + idLocation;
    getDeliveryOrders(1, value, string);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { idCustomer, idLocation } = this.props;
    this.setState({
      filter: {
        idCustomer,
        idLocation,
        idSalesOrder: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      },
      search: null
    }, () => this.handleFilter());
  };

  //Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    const { getDeliveryOrders, idCustomer, idLocation } = this.props;
    //const { current_page_do } = this.props.customer;
    const { filter } = this.state;
    let string = "";

    // if (filter.idDeliveryLocation) {
    //   string += "id_delivery_location=" + filter.idDeliveryLocation;
    // }
    // if (filter.status) {
    //   string
    //     ? (string += "&status=" + filter.status)
    //     : (string += "status=" + filter.status);
    // }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    // if (filter.id_customer) {
    //   string
    //     ? (string += "&id_customer=" + filter.id_customer)
    //     : (string += "id_customer=" + filter.id_customer);
    // }
    if (filter.idSalesOrder) {
      string
        ? (string += "&id_sales_order=" + filter.idSalesOrder)
        : (string += "id_sales_order=" + filter.idSalesOrder);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }
    string
      ? (string += "&id_customer=" + idCustomer + "&id_location=" + idLocation)
      : (string += "id_customer=" + idCustomer + "&id_location=" + idLocation);
    // console.log(string);
    this.setState({ search: null });
    getDeliveryOrders(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { idCustomer, idLocation } = this.props;
    this.setState({
      filter: {
        idCustomer,
        idLocation,
        idSalesOrder: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      },
      search: null
    }, () => this.handleFilter());
  };

  // Handle pagination
  handlePageChange = page => {
    const { getDeliveryOrders } = this.props;
    const { search, filter } = this.props.salesInvoice;
    getDeliveryOrders(page, search, filter);
  };

  render() {
    const {
      customers,
      deliveryOrders,
      idCustomer,
      idLocation,
      locations,
      // salesOrders,
      visible
    } = this.props;
    const { loadingForeign, loadingDeliveryOrders } = this.props
      ? this.props
      : false;

    const columns = [
      {
        title: getLanguage({ id: "deliveryOrderNo" }),
        dataIndex: "delivery_order_no",
        rowKey: "delivery_order_no",
        width: "15%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "approved";
              break;
            case 3:
              className = "rejected";
              break;
            case 4:
              className = "invoiced";
              break;
            case 5:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }
  
          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <Tooltip
                placement="bottom"
                title={<IntlMessages id="tooltip.select" />}
              >
                <a
                  onClick={e => {
                    e.preventDefault();
                    this.handleSelect(row.id_delivery_order);
                  }}
                  href="javascript:;"
                >
                  <TransNo>{text}</TransNo>
                </a>
              </Tooltip>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "salesOrders.salesOrderNo" }),
        dataIndex: "sales_order_no",
        rowKey: "sales_order_no",
        width: "15%"
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "delivery_order_date",
        rowKey: "delivery_order_date",
        width: "15%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "sidebar.customer" }),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "15%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
            case 2:
              className = "approved";
              display = getLanguage({ id: "approved" });
              break;
            case 3:
              className = "rejected";
              display = getLanguage({ id: "rejected" });
              break;
            case 4:
              className = "invoiced";
              display = getLanguage({ id: "invoiced" });
              break;
            case 5:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }
  
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const dataSource = [];
    Object.keys(deliveryOrders).map((item, index) => {
      return dataSource.push({
        ...deliveryOrders[item],
        key: item
      });
    });

    const { current_page_do, page_size_do, total_do } = this.props.salesInvoice
      ? this.props.salesInvoice
      : [];

    const showFirstTransaction = false;

    return (
      <Modal
        title={<IntlMessages id="salesInvoices.importDO" />}
        visible={visible}
        centered
        footer={null}
        keyboard={true}
        onCancel={showFirstTransaction =>
          this.props.handleModal(showFirstTransaction)
        }
        width="95%"
      >
        <Row style={{ marginBottom: "20px" }} type="flex">
          <Col span={18}>
            <Search
              placeholder={
                `${getLanguage({ id: "deliveryOrderNo" })}, ` +
                `${getLanguage({ id: "createdBy" })}`
              }
              onSearch={this.handleSearch}
              style={{ width: "99%", height: "36px" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
          </Col>
          <Col span={6}>
            <Button
              type="primary"
              onClick={this.handleSearch.bind(this, this.state.search)}
              style={{ marginRight: "2%" }}
            >
              <IntlMessages id="button.search" />
            </Button>

            <Button onClick={this.handleResetSearch}>
              <IntlMessages id="filter.reset" />
            </Button>
          </Col>
        </Row>
        <Row style={{ marginBottom: "20px" }}>
          <Col>
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Input.Group>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="sidebar.customer" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_customer"
                        )}
                        value={
                          this.state.filter.idCustomer
                            ? this.state.filter.idCustomer
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="salesOrders.filter.select.customer" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        // disabled={
                        //   this.props.firstShowTransaction ? false : true
                        // }
                        disabled={true}
                        //showArrow={false}
                      >
                        {customers && customers.length > 0
                          ? customers.map((data, key) => (
                              <Option key={key} value={data.id_customer}>
                                {`${data.customer_code} (${
                                  data.customer_name
                                })`}
                              </Option>
                            ))
                          : null}
                      </Select>
                    </Col>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="locations.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "idLocation"
                        )}
                        value={
                          this.state.filter.idLocation
                            ? this.state.filter.idLocation
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="purchaseReceipts.filter.select.location" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        // disabled={
                        //   this.props.firstShowTransaction ? false : true
                        // }
                        disabled={true}
                        //showArrow={false}
                      >
                        {locations && locations.length > 0
                          ? locations.map((data, key) => (
                              <Option key={key} value={data.id_location}>
                                {`${data.location_code} (${
                                  data.location_name
                                })`}
                              </Option>
                            ))
                          : null}
                      </Select>
                    </Col>
                  </Input.Group>
                </Row>
                <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "24px" }}
                >
                  <Input.Group>
                    {/* <Col span={12}>
                      <label>
                        <IntlMessages id="sidebar.salesOrder" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "idSalesOrder"
                        )}
                        value={
                          this.state.filter.idSalesOrder
                            ? this.state.filter.idSalesOrder
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="salesOrders.no.select" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        //showArrow={false}
                      >
                        {salesOrders && salesOrders.length > 0
                          ? salesOrders.map((data, key) =>
                              salesOrders[key].transaction_status !== 4 ? (
                                <Option key={key} value={data.id_sales_order}>
                                  {data.sales_order_no}
                                </Option>
                              ) : null
                            )
                          : null}
                      </Select>
                    </Col> */}
                    <Col span={12}>
                      <label>
                        <IntlMessages id="purchaseReceipts.filter.date" />
                      </label>
                      <br />
                      <RangePicker
                        format="DD MMMM YYYY HH:mm:ss"
                        showTime
                        allowClear={true}
                        style={{ width: "100%" }}
                        value={
                          this.state.filter.start_date &&
                          this.state.filter.end_date
                            ? [
                                moment(this.state.filter.start_date),
                                moment(this.state.filter.end_date)
                              ]
                            : []
                        }
                        onChange={this.handleFilterChange.bind(this, "date")}
                      />
                    </Col>
                    <Col span={12}>
                      <Row type="flex" justify="start">
                        <Col span={8}>
                          <label>
                            <IntlMessages id="dataPerPage" />
                          </label>
                          <br />
                          <Select
                            showSearch
                            onChange={this.handleFilterChange.bind(
                              this,
                              "pagination"
                            )}
                            value={
                              this.state.filter.pagination
                                ? this.state.filter.pagination
                                : page_size_do
                            }
                            style={{ width: "80%" }}
                            // placeholder={
                            //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                            // }
                            optionFilterProp="children"
                            filterOption={true}
                            //allowClear={true}
                            //showArrow={false}
                          >
                            <Option key={1} value={10}>
                              10
                            </Option>
                            <Option key={2} value={25}>
                              25
                            </Option>
                            <Option key={3} value={50}>
                              50
                            </Option>
                            <Option key={4} value={100}>
                              100
                            </Option>
                          </Select>
                        </Col>

                        <Col
                          span={6}
                          style={{
                            marginTop: "auto"
                          }}
                        >
                          <Button
                            size="small"
                            type="primary"
                            style={{ marginRight: "10px" }}
                            onClick={this.handleFilter}
                          >
                            <IntlMessages id="filter.submit" />
                          </Button>
                          <Button onClick={this.handleResetFilter} size="small">
                            <IntlMessages id="filter.reset" />
                          </Button>
                        </Col>
                      </Row>
                    </Col>
                  </Input.Group>
                </Row>
              </Panel>
            </Collapse>
          </Col>
        </Row>
        <Row>
          <Col>
            {dataSource.length === 0 && !loadingDeliveryOrders ? (
              idLocation && idCustomer ? (
                <Empty
                  description={
                    <IntlMessages id="salesInvoices.noDeliveryOrdersData" />
                  }
                />
              ) : (
                <Empty
                  description={
                    <IntlMessages id="salesOrders.item.noSelection" />
                  }
                />
              )
            ) : (
              <Table
                dataSource={dataSource}
                columns={columns}
                loading={loadingDeliveryOrders}
                expandedRowRender={record =>
                  expandedRowRender(record.delivery_order_details)
                }
                expandIcon={props => (
                  <Button
                    onClick={e => props.onExpand(props.record, e)}
                    size="small"
                  >
                    <IntlMessages id="button.view" />
                  </Button>
                )}
                // Set pagination, jika ada page_size_do mengunakan pagination API
                // jika tidak ada page_size_do menggunakan pagination bawaan
                pagination={
                  page_size_do
                    ? {
                        current: current_page_do,
                        pageSize: parseInt(page_size_do),
                        total: total_do,
                        onChange: this.handlePageChange.bind(this)
                      }
                    : true
                }
              />
            )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(
  connect(
    null,
    { getDeliveryOrders }
  )
)(ModalImportDO);
