const actions = {
  getSalesInvoices: (page, search, filter) => ({
    type: "GET_SALES_INVOICES_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createSalesInvoice: () => ({
    type: "CREATE_SALES_INVOICE_REQUEST"
  }),
  getItemsForSI: (
    id_customer,
    id_location,
    date,
    id_payment_category,
    search
  ) => ({
    type: "GET_FILTER_ITEMS_BY_CUSTOMER_SI_REQUEST",
    payload: {
      id_customer: id_customer,
      id_location: id_location,
      id_payment_category,
      date: date,
      search: search
    }
  }),
  getDiscounts: data => ({
    type: "GET_DISCOUNT_SI_REQUEST",
    payload: { body: data }
  }),
  getTransactions: (page, search, filter) => ({
    type: "GET_FILTER_SALES_INVOICES_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getDeliveryOrders: (page, search, filter) => ({
    type: "GET_FILTER_DELIVERY_ORDERS_SI_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  storeSalesInvoice: data => ({
    type: "STORE_SALES_INVOICE_REQUEST",
    payload: { body: data }
  }),
  editSalesInvoice: (id_sales_invoice, search) => ({
    type: "EDIT_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice: id_sales_invoice, search: search }
  }),
  updateSalesInvoice: (id_sales_invoice, data) => ({
    type: "UPDATE_SALES_INVOICE_REQUEST",
    payload: {
      id_sales_invoice: id_sales_invoice,
      body: data
    }
  }),
  showSalesInvoice: id_sales_invoice => ({
    type: "SHOW_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice: id_sales_invoice }
  }),
  confirmSalesInvoice: (id_sales_invoice, data) => ({
    type: "CONFIRM_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice, body: data }
  }),
  unconfirmSalesInvoice: id_sales_invoice => ({
    type: "UNCONFIRM_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice }
  }),
  closeSalesInvoice: id_sales_invoice => ({
    type: "CLOSE_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice }
  }),
  cancelSalesInvoice: (id_sales_invoice, data) => ({
    type: "CANCEL_SALES_INVOICE_REQUEST",
    payload: { id_sales_invoice, body: data }
  }),
  uploadDocument: (id_sales_invoice, data) => ({
    type: "UPLOAD_SALES_INVOICE_DOCUMENT_REQUEST",
    payload: {
      id_sales_invoice,
      body: data
    }
  }),
  downloadDocument: id_sales_invoice_document => ({
    type: "DOWNLOAD_SALES_INVOICE_DOCUMENT_REQUEST",
    payload: { id_sales_invoice_document }
  }),
  deleteDocument: (id_sales_invoice_document, id_sales_invoice) => ({
    type: "DELETE_SALES_INVOICE_DOCUMENT_REQUEST",
    payload: {
      id_sales_invoice_document: id_sales_invoice_document,
      id_sales_invoice: id_sales_invoice
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  }),
  setTaxBase: tax_bases => ({
    type: "SET_TAX_BASES_SALES_INVOICE",
    payload: { tax_bases: tax_bases }
  }),
  getUpdateSIDetails: data => ({
    type: "GET_UPDATE_SALES_INVOICE_DETAILS_REQUEST",
    payload: {
      body: data
    }
  })
};

export default actions;
